<template>
    <div>
        <tarjeta :trans="translations"
                 @cardChanged="cambiar_tipo"
                 @change="cambiar_tarjeta"></tarjeta>
        <button v-if="!procesando_pago" @click="pagar">Pagar {{ importe }}</button>
    </div>
</template>

<script>

import VCreditCard from 'v-credit-card';
import 'v-credit-card/dist/VCreditCard.css';

export default {  
    data() {
        return {
            translations: {
                name: {
                    label: 'Nombre',
                    placeholder: 'Nombre completo'
                },
                card: {
                    label: 'Número de tarjeta',
                    placeholder: 'Número de tarjeta'
                },
                expiration: {
                    label: 'Expiration'
                },
                security: {
                    label: 'Código de seguridad',
                    placeholder: 'Código'
                }
            },
            tarjeta: {
                nombre: '',
                numero: '',
                exp: '',
                cvc: '',
                tipo: ''
            },
            stripe_key: 'pk_test_51P6NNORqy2tbEdu05ADMpIOU9zDcWoJmP3kUgNqwghI6F5KScggAdjRUFjmoxMwK3W0vyaf1LXAH4LZ4IAeb9SXS00ftcvUn8U'
            ,stripeLoaded: false
            ,procesando_pago:false
        }
    },
    methods: {
        cambiar_tarjeta(tarjeta) {
            this.tarjeta.nombre = tarjeta.name;
            this.tarjeta.numero = tarjeta.cardNumber;
            this.tarjeta.exp = tarjeta.expiration;
            this.tarjeta.cvc = tarjeta.security;
            console.log(this.tarjeta);
        },
        cambiar_tipo(cardName) {
            this.tarjeta.tipo = cardName;
            console.log(cardName);
        },
        pagar() {
            var tmp = this.tarjeta.exp.split('/');
            console.log('Ya esta dividida la fecha ', tmp);
            if (this.stripeLoaded) {
                // eslint-disable-next-line 
                Stripe.createToken({
                    number: this.tarjeta.numero,
                    cvc: this.tarjeta.cvc,
                    exp_month: tmp[0],
                    exp_year: tmp[1]
                }, (status, response) => {
                    if (response.error) {
                        alert('Error al pedir token');
                    } else {
                        var xhr = new XMLHttpRequest();
                        var self=this;
                        xhr.open('POST',this.url,true);
                        xhr.onreadystatechange = function(){
                            if(this.readyState == 4){
                                if(this.status == 200){
                                    self.procesando_pago=true;
                                    var datos=JSON.parse(this.responseText);

                                    if(datos.status=='OK'){
                                        alert('Si se pudo el pago!!');
                                    }else{
                                        alert("No se pudo el pago");
                                    }
                                }
                                else{
                                    console.log('Algo trono, llamale al programador')
                                }
                            }
                        }
                        var data = new FormData();
                        data.append('token_stripe', response['id']);
                        data.append('precio',this.importe);
                        data.append('item',this.item);
                        data.append('medio','Stripe');
                        data.append('idusuario',this.idusuario);
                        xhr.send(data);
                        //console.log('Esto es toda la respuesta', response);
                        //console.log('Esto es el token', response.id);
                    }
                });
            } else {
                console.error('Stripe no ha sido cargado aún.');
            }
        }
    },
    props: ['importe','item','idusuario','url'],
    components: {
        'tarjeta': VCreditCard
    },
    created() {
        // Se ejecuta después de que el componente haya sido creado.
        // eslint-disable-next-line 
        Stripe.setPublishableKey(this.stripe_key);
        this.stripeLoaded = true;
    }
};
</script>
